#marketplaceContextContainer {
    z-index: var(--z-depth);
}


.selectOption {
    width: 100%;
    height: 85px;
    min-height: 50px;
    border-radius: 15px;
    box-shadow: inset 0 0 6px 0 black;
    text-align: left;
}

.selectOption:hover {
    cursor: pointer;
}

.selectOption:nth-child(1){
    background-image: url('../assets/images/halo_infinite_cover.png');
    background-size: cover;
    background-repeat: no-repeat;
}

.selectOption:nth-child(2){
    background-image: url('../assets/images/xbox_media.png');
    background-position: 50% 50%;
    background-size: 30%;
    background-repeat: no-repeat;
    background-color: white;
}

.selectOption:nth-child(3){
    background-image: url('../assets/images/demo_img.png');
    background-size: cover;
    background-position: 50% 20%;
    background-repeat: no-repeat;
    background-color: white;
}

.selectOption:nth-child(4){
    background-image: url('../assets/images/media_img.png');
    background-size: cover;
    background-position: 50% 20%;
    background-repeat: no-repeat;
}

.selectOption:nth-child(5){
    background-image: url('../assets/images/Themes\ and\ Gamerpictures.png');
    background-size: 80%;
    background-position: 100% 50%;
    background-repeat: no-repeat;
}

.selectOption:nth-child(6){
    background-image: url('../assets/images/woman_megaphone.png');
    background-size: cover;
    background-position: 50% 20%;
    background-repeat: no-repeat;
}

.selectOption:not(:nth-last-child(1)){
    /* margin-bottom: 10px; */
    margin-bottom: 10px;
}

.selectOption > p { 
    position: relative;
    top: 65%;
    left: 3%;
    font-size: 1.3rem;
    font-weight: 600;
}



.imageHeaderContainer {
    min-height: 350px;
    width: 100%;
    border-radius: var(--border_radius);
    box-shadow: inset 0 0 6px 0 black;
    background-image: url('../assets/images/marketplace_arcade.png');
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
}

.selectItemListContainer {
    position: relative;
    width: 100%;
    height: 100%;
    /* background-color: blue; */
}

.innerListContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: calc(100% - 30px);
    margin-top: 15px;
}

.innerListContainer > .listItem {
    display: flex;
    height: 50px;
    width: 100%;
    /* background-color: chocolate; */
}

.listItem:first-child:before {
    content: "";
    position: absolute;
    width: inherit;
    margin-top: -5px;
    height: 2px;
    background-color: rgba(9, 9, 9, 0.342);
}

.listItem:after {
    content: "";
    position: absolute;
    width: inherit;
    margin-top: 55px;
    height: 2px;
    background-color: rgba(9, 9, 9, 0.514);
}

.listItem > .listIcon {
    width: 75px;
}

.listItem > p {
    padding-top: 15px;
    font-size: 1.2rem;
}

.card_icon {
    background-image: url('../assets/icons/card_icon.png');
    background-size: 60%;
    background-repeat: no-repeat;
    transform: rotate(-15deg) translate(15%, 20%);
}

.download_icon {
    background-image: url('../assets/icons/download_icon.png');
    background-size: 60%;
    background-repeat: no-repeat;
    transform: translate(15%, 5%);
}

.crown_icon {
    background-image: url('../assets/icons/crown_icon.png');
    background-size: 60%;
    background-repeat: no-repeat;
    transform: rotate(-15deg) translate(15%, 5%);
}

@media screen and (max-height: 575px) {

    .selectOption {
        max-width: 280px;
        max-height: 75px;
    }

    .selectOption:nth-child(3){
        display: none;
    }
    .selectOption:nth-child(4){
        display: none;
    }

    .selectOption > p {
        font-size: 1rem;
    }


    .imageHeaderContainer {
        min-height: 200px;
    }


}